import React from "react"
import styled from "styled-components"
import MyButton from "../atoms/Button"

const StyledContainer = styled.div`
  overflow: none;
  @media (max-width: 1000px) {
    overflow: hidden;
    position: relative;
    width: 100%;
  }
`
const StyledContent = styled.div`
  position: absolute;
  right: 0;
  padding: 40px;
  text-align: center;
  background: #4a4a4a;
  max-width: 400px;
  color: #fff;
  @media (max-width: 1000px) {
    position: unset;
    padding-bottom: 70px;
    width: 100%;
    max-width: none;
  }
  @media (min-width: 1001px) {
    border: 3px solid ${props => props.color};
    :before {
      content: "";
      position: absolute;
      top: 50px;
      left: -3px;
      z-index: -5;
      width: calc(100% + 6px);
      height: calc(100% - 100px);
      background: #4a4a4a;
      transition: 0.5s ease-in-out;
      transform: scaleY(1);
      transition-delay: 0.3s;
    }
    :hover:before {
      transform: scaleY(0);
    }
    :after {
      z-index: -5;
      content: "";
      position: absolute;
      left: 50px;
      top: -3px;
      height: calc(100% + 6px);
      width: calc(100% - 100px);
      background: #4a4a4a;
      transition: 0.5s ease-in-out;
      transform: scaleX(1);
    }
    :hover:after {
      transform: scaleX(0);
    }
    :hover .button {
      :before {
        transform-origin: right;
        transition: transform 0.5s ease;
        transform: scaleX(0);
      }
    }
  }
`
const StyledTitle = styled.h2`
  font-size: 2vw;
  font-weight: 500;
  margin: 0;
  text-align: right;
  position: relative;
  color: ${props => props.color};
  @media (max-width: 1300px) {
    font-size: 3vw;
  }
  @media (max-width: 1000px) {
    font-size: 7vw;
    text-align: center !important;
  }
`

const StyledLinkContainer = styled.h5`
  font-size: 1vw;
  font-weight: 300;
  margin: 0;
  text-align: right;
  position: relative;
  @media (max-width: 1300px) {
    font-size: 2vw;
  }
  @media (max-width: 1000px) {
    font-size: 3.5vw;
    text-align: center !important;
  }
`

const StyledLink = styled.a`
  color: #ccc;
  font-weight: 400;
  text-decoration: none;
  :hover {
    font-weight: 500;
  }
`

const StyledSpan = styled.p`
  font-size: 0.8em;
  font-weight: 300;
  position: relative;
  text-align: right;
  @media (max-width: 1000px) {
    text-align: justify !important;
    text-justify: inter-character;
  }
  @media (max-width: 500px) {
    font-size: 0.7em;
  }
`

const WebsiteContent = ({
  color,
  active,
  title,
  link,
  description,
  technologies,
}) => (
  <StyledContainer>
    <StyledContent
      color={color}
      className={`${active} websiteContent vertical-parallax`}
    >
      <StyledTitle className="contentTitle" color={color}>
        {title}
      </StyledTitle>
      <StyledLinkContainer className="link">
        <StyledLink href={link} target="_blank">
          {link}
        </StyledLink>
      </StyledLinkContainer>
      <StyledSpan className="contentDescription">
        {description}
        <span style={{ display: "block", marginTop: "20px" }}>
          Used technologies: {technologies}
        </span>
      </StyledSpan>
      <MyButton href={link} color={color} text="GO TO" />
    </StyledContent>
  </StyledContainer>
)

export default WebsiteContent
