import React from "react"
import styled from "styled-components"
import { ReactLogo } from "@styled-icons/fa-brands/ReactLogo"
import { Css3 } from "@styled-icons/boxicons-logos/Css3"
import { Html5 } from "@styled-icons/boxicons-logos/Html5"
import { JsSquare } from "@styled-icons/fa-brands/JsSquare"
import { Bootstrap } from "@styled-icons/boxicons-logos/Bootstrap"
import { Gatsby } from "@styled-icons/simple-icons/Gatsby"
import { Devices } from "@styled-icons/material/Devices"

const StyledContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 500px;
  justify-content: center;
  margin: auto;
  @media (max-width: 520px) {
    width: unset;
  }
`

const IconWrapper = styled.div`
  overflow: hidden;
  border: 2px solid ${props => props.color};
  border-radius: 10%;
  transform: rotate(45deg);
  margin: 0 20px;
  @media (max-width: 450px) {
    margin: 15px 10px;
  }
  .icon {
    margin: 20px;
    transform: rotate(-45deg);
    transition: 0.5s ease;
    width: 40px;
    height: 40px;
    @media (max-width: 600px) {
      width: 25px;
      height: 25px;
      margin: 10px;
    }
  }
  :hover {
    .icon {
      color: #fff !important;
    }
  }

  :after {
    content: "";
    display: block;
    z-index: -5;
    width: calc(100% + 1px);
    height: 100%;
    top: -100%;
    left: 0;
    right: 0;
    margin: auto;
    transition: all 0.2s linear 0s;
    position: absolute;
    background-color: ${props => props.color};
  }

  :hover {
    :after {
      top: 0%;
      transition: all 0.2s linear 0s;
    }
  }
`

const TechnologyIcons = () => (
  <StyledContainer>
    <IconWrapper color="#67e09a" title="Responsive Web Design">
      <Devices
        className="icon"
        style={{
          color: "#67e09a",
        }}
        alt="Responsive Web Design"
      />
    </IconWrapper>
    <IconWrapper color="#61dafb" title="React">
      <ReactLogo
        className="icon"
        style={{
          color: "#61dafb",
        }}
        alt="React"
      />
    </IconWrapper>
    <IconWrapper color="#0070ba" title="CSS">
      <Css3
        className="icon"
        style={{
          color: "#0070ba",
        }}
        alt="CSS"
      />
    </IconWrapper>
    <IconWrapper color="#ff8d03" title="Html">
      <Html5
        className="icon"
        style={{
          color: "#ff8d03",
        }}
        alt="Html"
      />
    </IconWrapper>
    <IconWrapper color="#663399" title="Gatsby">
      <Gatsby
        className="icon"
        style={{
          color: "#663399",
        }}
        alt="Gatsby"
      />
    </IconWrapper>
    <IconWrapper color="#63a814" title="Javascript">
      <JsSquare
        className="icon"
        style={{
          color: "#63a814",
        }}
        alt="Javascript"
      />
    </IconWrapper>
    <IconWrapper color="#7952b3" title="Bootstrap">
      <Bootstrap
        className="icon"
        style={{
          color: "#7952b3",
        }}
        alt="Bootstrap"
      />
    </IconWrapper>
  </StyledContainer>
)

export default TechnologyIcons
